@font-face {
  font-family: "Oswald";
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
body {
  min-width: 320px;
  width: 100%;
  font-family: "Oswald", "Arial", sans-serif;
  margin: 0;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
  overflow-x: hidden;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.container {
  width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 30px;
  }
}
@media (min-width: 1320px) {
  .container {
    width: 1300px;
    padding: 0 40px;
  }
}
.button {
  display: block;
  width: 100%;
  color: #ffffff;
  padding: 10px;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  background-color: #68B738;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.page-header {
  background-color: #ffffff;
}
.page-header__logo {
  position: relative;
  display: block;
  width: 190px;
  margin-bottom: 14px;
}
.page-header__logo:hover {
  opacity: 0.8;
}
.page-header__logo:active {
  opacity: 0.6;
}
.page-header__logo--line::after {
  content: "";
  position: absolute;
  left: -20px;
  bottom: -15px;
  width: 320px;
  height: 1px;
  background-color: #e6e6e6;
}
@media (min-width: 768px) {
  .page-header__logo {
    width: 174px;
    height: 52px;
    margin-top: 25px;
  }
  .page-header__logo--line::after {
