.site-list__link {
  display: block;
  padding: 22px 10px 20px 10px;
  color: #000000;
}
@media (min-width: 768px) {
  .site-list__link--active {
    position: relative;
  }
  .site-list__link--active::after {
    content: "";
    position: absolute;
    background-color: #68B738;
    height: 2px;
    width: calc(100% - 20px);
    left: 10px;
    bottom: 11px;
  }
}
@media (min-width: 1320px) {
  .site-list__link--index {
    color: #ffffff;
