.form__input-radio + label::before {
  content: "";
  position: absolute;
  top: -5px;
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .form__input-radio + label::before {
    width: 40px;
    height: 40px;
    top: -7px;
    left: 27px;
  }
}
.form__input-radio:checked + label::after {
  content: "";
  position: absolute;
  width: 14px;
  height: 14px;
  top: 3px;
  left: 8px;
  border: none;
  background-color: #68B738;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .form__input-radio:checked + label::after {
    width: 18px;
    height: 18px;
    top: 4px;
    left: 38px;
  }
}
.form__input-checkbox + label::before {
  content: "";
  position: absolute;
  top: -4px;
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
}
@media (min-width: 768px) {
  .form__input-checkbox + label::before {
    top: -7px;
    width: 40px;
    height: 40px;
  }
}
.form__input-checkbox:checked + label::after {
  content: "";
  position: absolute;
  top: 3px;
  left: 5px;
  width: 21px;
  height: 16px;
  background-image: url("../img/icon-tick-green.svg");
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .form__input-checkbox:checked + label::after {
    top: 6px;
    left: 12px;
    width: 18px;
    height: 14px;
  }
}
.form__input-radio:hover + label::before,
.form__input-checkbox:hover + label::before {
  border: 1px solid #bababa;
}
.form__input-radio:disabled + label,
.form__input-checkbox:disabled + label {
  opacity: 0.3;
}
.form__input-radio:focus + label,
.form__input-checkbox:focus + label {
  text-decoration: underline;
}
